import React from 'react';
import s from './Todo.module.css'
import {FilterType, ListType} from './../HW2';
import {Button, Grid, IconButton} from '@material-ui/core';
import {Backspace} from '@material-ui/icons';
import {green, red} from '@material-ui/core/colors';

type PropsType = {
    task: Array<ListType>
    removeTask: (id: number) => void
    changeTask: (value: FilterType) => void
}

function Todo(props: PropsType) {
    return (
        <div>
            {props.task.map(t => {
                return (
                    <div key={t.id} className={s.list_block}>
                        <div className={s.list_item}>
                            <p>{t.task}</p>
                            <div className={t.importance}></div>

                            <IconButton aria-label="delete"
                                        onClick={() => {
                                            props.removeTask(t.id)
                                        }} className={s.remove_button}>
                                <Backspace style={{color: red[500]}}/>
                            </IconButton>
                        </div>
                    </div>
                )
            })}
            <Grid container direction="row" justify="center"
                  alignItems="center" spacing={1}>
                <Grid item>
                    <Button variant="contained" onClick={() => {
                        props.changeTask('all')
                    }}>All</Button>
                </Grid>
                <Grid item>
                    <Button variant="contained" onClick={() => {
                        props.changeTask('high')
                    }}>High</Button>
                </Grid>
                <Grid item>
                    <Button variant="contained" onClick={() => {
                        props.changeTask('medium')
                    }}>Medium</Button>
                </Grid>
                <Grid item>
                    <Button variant="contained" onClick={() => {
                        props.changeTask('low')
                    }}>Low</Button>
                </Grid>
            </Grid>
        </div>
    );
}

export default Todo;