import React, {useState} from 'react';
import {Box, FormControlLabel, Grid, Switch} from '@material-ui/core';
import HW2 from '../../../h2/HW2';
import HW1 from '../../../h1/HW1';
import s from './PreJunior.module.css'

function PreJunior() {

    return (
        <Grid container
              spacing={5}
              direction="row"
              justify="center"
              alignItems="stretch"
        >
            <Grid item xs={12} sm={6} className={s.grid}>
                <Box fontWeight="fontWeightRegular" m={1} letterSpacing={2}>
                    HOMEWORK 2
                </Box>
                <HW2/>
            </Grid>
            <Grid item xs={12} sm={6}>
                <Box fontWeight="fontWeightRegular" m={1} letterSpacing={2}>
                    HOMEWORK 1
                </Box>
                <HW1/>
                <Box fontWeight="fontWeightLight" m={1} letterSpacing={1} textAlign={'center'} fontSize={20}>
                    Components can refer to other components in their output. This lets us use the same component
                    abstraction for any level of detail. A button, a form, a dialog, a screen: in React apps, all
                    those are commonly expressed as components.
                </Box>
            </Grid>
        </Grid>
    );
}

export default PreJunior;
