import React, {useState} from 'react';
import Routes from './Routes';
import {HashRouter} from 'react-router-dom';
import s from './HW5.module.css'
import {NavLink} from 'react-router-dom';

function HW5() {
    var [toggle, setToggle] = useState<boolean>(false)
    var style1 = {'--i': '0.05s'} as React.CSSProperties;
    var style2 = {'--i': '0.1s'} as React.CSSProperties;
    var style3 = {'--i': '0.15s'} as React.CSSProperties;
    var style4 = {'--i': '0.2s'} as React.CSSProperties;
    return (
        < HashRouter>
            <div className={toggle ? `${s.container} ${s.active}` : s.container}>
                <div className={s.navbar}>
                    <div className={s.menu}>
                        <h3 className={s.logo}>IT<span>incubator</span></h3>
                        <div className={s.hamburger_menu} onClick={() => {
                            setToggle(!toggle)
                        }}>
                            <div className={s.bar}></div>
                        </div>
                    </div>
                </div>

                <div className={s.main_container}>
                    <div className={s.main}>
                        <header>
                            <div className={s.overlay}>
                                <div className={s.inner}>
                                    <Routes/>
                                </div>
                            </div>
                        </header>
                    </div>

                    <div className={s.shadow + ' ' + s.one}></div>
                    <div className={s.shadow + ' ' + s.two}></div>
                </div>


                <div className={s.links}>
                    <ul>
                        <li>
                            <NavLink to='/start-page' style={style1}>Start</NavLink>
                        </li>
                        <li>
                            <NavLink to='/pre-junior' style={style2}>PreJunior</NavLink>
                        </li>
                        <li>
                            <NavLink to='/junior' style={style3}>Junior</NavLink>
                        </li>
                        <li>
                            <NavLink to='/junior-plus' style={style4}>JuniorPlus</NavLink>
                        </li>
                    </ul>
                </div>
            </div>
        </HashRouter>
    );
}

export default HW5;