import React from 'react';
import s from './Junior.module.css'
import HW7 from '../../../h7/HW7';
import {Box, Grid} from '@material-ui/core';

function Junior() {

    return (
        <Grid container
              spacing={5}
              direction="row"
              justify="center"
              alignItems="stretch"
        >
            <Grid item xs={12} sm={12} className={s.grid}>
                <Box fontWeight="fontWeightRegular" m={1} letterSpacing={2}>
                    HOMEWORK 7
                </Box>
                <HW7/>
            </Grid>
        </Grid>
    );
}

export default Junior;
